aside {
  // overflow-y: auto;
  background-color: #2D333A;
  z-index: 1;
  user-select: none;
  // height: 500px;
  position: fixed;
  width: 350px;
  left: 25px;
  top: calc(69px + 25px);
  border-radius: 5px;
  box-shadow: 0 0 20px rgba(0, 0, 0, .5);
  outline: 5px solid rgba(0, 0, 0, .25);

  max-height: calc(100vh - 69px - 50px);
  overflow-y: auto;

  #map_panel {

    .no_image {
      opacity: .25;
    }

    user-select: text;

    > * {
      width: calc(100% - 40px);
      margin: 10px 20px;
      display: block;
    }

    #div_coord {
      border-top: rgba(0, 0, 0, 0.25) solid 1px;
      border-bottom: rgba(0, 0, 0, 0.25) solid 1px;
      padding: 10px 0px;
    }

    #map_background {
      box-shadow: 0 -140px 75px -70px rgba(0, 0, 0, 0.5) inset;
      width: 100%;
      margin: 0;
      height: 200px;
      background-size: cover;
      background-position: center;
    }

    #map_nom {
      margin: -50px 0 0 20px;
      font-size: 25px;
      color: white;
      filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.75));
      font-weight: 600;
    }

    #map_type {
      opacity: .5;
      margin-top: 35px;
      // margin: 35px 0 5px 20px;
      font-size: 18px;
    }

    #map_commentaire {
      font-size: 14px;
      font-style: italic;
      opacity: .5;

      &:before, &:after {
        content: '"';
      }
    }

    #map_description {
      font-size: 16px;
    }

    #map_discord, #map_forum, #map_dynmap {
      display: flex;
      align-items: center;
      margin: 5px 20px;
    }

    #map_discord::before {
      content: url(../icons/discord.png);
    }

    #map_forum::before {
      content: url(../icons/flarum.png);
    }

    #map_dynmap::before {
      content: url(../icons/dynmap.png);
    }

    #map_discord::before, #map_forum::before, #map_dynmap::before {
      height: 20px;
      width: 20px;
      display: inline-flex;
      margin-right: 5px;
    }

    #map_updated {
      opacity: .5;
      font-size: 12px;
      margin-top: 10px;
    }

  }

  #map_options {

    height: 0px;
    overflow: hidden;

    > div {
      display: flex;
      // border-bottom: solid 1px rgba(0, 0, 0, 0.25);
      background-color: rgba(0, 0, 0, 0.125);
      margin: 0 0 5px 0;
      height: 40px;
      align-items: center;
      padding-left: 10px;

      h3 {
        font-weight: normal;
        user-select: none;
        font-size: 16px;
        pointer-events: none;
      }

      &.checked::before {
        content: "✔️";
        visibility: visible;
      }

      &::before {
        content: "❌";
        margin-right: 8px;
        visibility: hidden;
      }

      &:hover {
        background-color: rgba(255, 255, 255, 0.05);
      }
    }

    > p {
        font-size: 14px;
        opacity: .5;
        text-transform: uppercase;
        margin: 10px 0 5px 10px;
    }


  }

}
