header {
  display: flex;
  height: 69px;
  background-color: gray;
  background-color: #21252B;
  align-items: center;
  justify-content: space-between;
  padding: 0 50px;

  h1 {
    color: white;
    font-size: 17px;
  }

  ul {
    display: flex;
    list-style: none;

    li {
      &:hover {
        background-color: black;
      }

      a {
        padding: 0 20px;
        text-decoration: none;
        color: white;
        font-size: 22px;
      }
    }
  }
}
