.leaflet-marker-icon {
  border-radius: 50%;
  outline: 2px #FFF solid !important;
  outline-offset: -2px;
  box-shadow: 0 0 0px 2px rgba(0, 0, 0, 0.25);
  filter: brightness(1.2);
}

.leaflet-tooltip {
  animation: appear .25s ease;
}

@keyframes appear {
  0% {
    top: -6.9px;
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    top: 0px;
  }
}
